@import './base.scss';

#results{
  pre {
    max-height: 40vh;
    border: 1px solid black;
    padding: 20px;
    margin-left: 30%;
    
    div {
      padding: 2px 0;
    }
  }
  h2{
    margin-left: 30%;
  }
}
#logo{
  width: 100%;
  display: flex;
  justify-content: center;
}
aside {
  margin-left: 0;
  display: inline;
  li {
    list-style-type: none;
  }
  ul {
    padding-left: 0;
    display: inline;
  }
}
